import contentJSON from '/json/content.json';
import {
    insertNext,
    getImageAssetURL,
    CONTENT_TYPE,
    buildVimeoEmbedCode,
} from '/js/utils';
import imageAssets from './../assets/img/*.*';

const expandContent = function (index: number, titleNode: HTMLElement) {
    // Reset the dom
    const containers = document.getElementsByClassName("contentContainer");
    for (const container of containers)
        container?.parentNode?.removeChild(container);

    // Construct doms 
    const project = contentJSON.projects[index];
    const targetDom = document.getElementById(project.id);
    const contentContainer = document.createElement('div');
    contentContainer.className = "content contentContainer";

    // Append to target
    insertNext(contentContainer, targetDom);

    // Contruct content doms
    let content: any = null;
    const targetDomWidth = parseFloat(getComputedStyle(selectedWorkNode as Element, null).getPropertyValue('width'));
    const info = project.content;
    switch (project.content.is) {
        case 'video':
            content = buildDescriptionNode(targetDomWidth, info, CONTENT_TYPE.VIDEO);
            break;

        case 'image':
            content = buildDescriptionNode(targetDomWidth, info, CONTENT_TYPE.IMAGE);
            break;

        case 'realtime':
            content = buildDescriptionNode(targetDomWidth, info, CONTENT_TYPE.REALTIME);
            break;
            
        default:
            break;
    }
    if(content !== null) contentContainer.appendChild(content);


    // Content info
    let contentTitle: string = '';
    for (let i = 0; i < 3; i++) {
        // Title
        switch (i) {
            case 0: contentTitle = 'Info'; break;
            case 1: contentTitle = 'Process'; break;
            case 2: contentTitle = 'Credit'; break;
            default: break;
        }

        const title = document.createElement('div');
        title.className = "contentTitle";
        title.innerHTML = "<h2>" + contentTitle + "</h2>";

        contentContainer.appendChild(title);

        // Description
        const description = document.createElement('div');
        description.className = "contentDescription";
        let infoDescription: any = null;
        let processDescription: any = null;
        switch (i) {
            case 0:
                for (const info of project.info) {
                    switch (info.is) {
                        case 'video':
                            infoDescription = buildDescriptionNode(targetDomWidth, info, CONTENT_TYPE.VIDEO);

                            break;
                        case 'image':
                            infoDescription = buildDescriptionNode(targetDomWidth, info, CONTENT_TYPE.IMAGE);
                            break;

                        case 'text':
                            infoDescription = buildDescriptionNode(targetDomWidth, info, CONTENT_TYPE.TEXT);
                            break;

                        default:
                            break;
                    }
                    if (infoDescription !== null) description.appendChild(infoDescription);
                }
                break;

            case 1:
                for (const process of project.process) {
                    switch (process.is) {
                        case 'video':
                            processDescription = buildDescriptionNode(targetDomWidth, process, CONTENT_TYPE.VIDEO);
                            break;

                        case 'image':
                            processDescription = buildDescriptionNode(targetDomWidth, process, CONTENT_TYPE.IMAGE);
                            break;

                        case 'text':
                            processDescription = buildDescriptionNode(targetDomWidth, process, CONTENT_TYPE.TEXT);
                            break;

                        default:
                            break;
                    }
                    if (processDescription !== null) description.appendChild(processDescription);
                }
                break;
            case 2:
                for (const credit of project.credit) {
                    const creditNode = buildCreditNode(credit);

                    if (creditNode !== null) description.appendChild(creditNode);
                }
                break;
            default:
                break;
        }
        if (description !== null) contentContainer.appendChild(description);
    }
    titleNode.scrollIntoView({ behavior: 'smooth' });
};

const buildCreditNode = function (info: any) {
    const paragraph = document.createElement('p') as HTMLElement;
    paragraph.innerHTML = '<credit>' + info.role + '</credit>: ' + info.credit;

    return paragraph;
}

const buildDescriptionNode = function (width: number, info: any, contentType: CONTENT_TYPE) {
    let domElement: any = null;

    switch (contentType) {
        case CONTENT_TYPE.VIDEO:
            domElement = document.createElement('div') as HTMLElement;
            domElement.className = "listThumbnailImageContainer";
            domElement.innerHTML = buildVimeoEmbedCode(info.url, true);
            break;

        case CONTENT_TYPE.IMAGE:
            domElement = document.createElement('img') as HTMLImageElement;
            domElement.src = getImageAssetURL(imageAssets, info.url);
            domElement.width = width;
            break;

        case CONTENT_TYPE.TEXT:
            domElement = document.createElement('p') as HTMLElement;
            domElement.innerHTML = info.p;
            break;

        default:
            break;
    }

    return domElement;
}

const buildContentNode = function (width: number, url: string, name:string, contentType: CONTENT_TYPE) {
    const content = document.createElement('div') as HTMLElement;

    if (contentType === CONTENT_TYPE.IMAGE) {
        // Image content
        content.innerHTML = '<img src=\"avseoul/assets/' + getImageAssetURL(imageAssets, url) + '\" width=\"'
            + width + '\" height="auto">';

    } else {
        // Video and realtime content
        content.style.cursor = 'pointer';
        content.style.opacity = '1';
        content.innerHTML = buildVimeoEmbedCode(url, true);
    }

    return content;
}

const buildContentContainerNode = function (this: any, contentData: any, nodeIndex: number) {
    const container = document.createElement('div') as HTMLElement;

    const thumbnailMask = document.createElement('div') as HTMLElement;
    let thumbnail: any = null;// = new HTMLElement();// = new Image() as HTMLImageElement;

    const descriptionMask = document.createElement('div') as HTMLElement;
    const subject = document.createElement('div') as HTMLElement;
    const description = document.createElement('div') as HTMLElement;
    const date = document.createElement('div') as HTMLElement;
    const detail = document.createElement('rainbow_p') as HTMLElement;

    // Node tree
    container.appendChild(thumbnailMask);
    container.appendChild(descriptionMask);

    descriptionMask.appendChild(subject);
    descriptionMask.appendChild(description);
    descriptionMask.appendChild(date);
    descriptionMask.appendChild(detail);

    // Nodes attributes
    container.className = 'listContainer';
    container.id = contentData.id;
    container.setAttribute('name', contentData.title);

    const targetDomWidth = parseFloat(getComputedStyle(selectedWorkNode as Element, null).getPropertyValue('width'));
    switch (contentData.content.is) {
        case 'video':
            thumbnail = buildContentNode(targetDomWidth, contentData.content.url, contentData.title, CONTENT_TYPE.VIDEO);
            break;

        case 'image':
            thumbnail = buildContentNode(targetDomWidth, contentData.content.url, contentData.title, CONTENT_TYPE.IMAGE);
            break;

        case 'realtime':
            thumbnail = buildContentNode(targetDomWidth, contentData.content.url, contentData.title, CONTENT_TYPE.REALTIME);
            break;

        default:
            break;
    }

    if (thumbnail !== null) {
        thumbnailMask.appendChild(thumbnail as HTMLElement);
        (thumbnail as HTMLElement).className = 'listThumbnailImage';
    }

    thumbnailMask.className = 'listThumbnailImageContainer';
    descriptionMask.className = 'listDescriptionContainer';

    subject.className = 'listSubject';
    subject.onclick = expandContent.bind(this, nodeIndex, subject);
    subject.style.cursor = 'pointer';
    subject.innerHTML = '<div><h2>' + contentData.title + '</h2></div>';

    description.className = 'listDescription';
    description.innerHTML = '<p>' + contentData.description + '</p>';

    date.className = 'listDate';
    date.innerHTML = '<p>' + contentData.date + '</p>';

    detail.className = 'listDetail';
    detail.onclick = expandContent.bind(this, nodeIndex, subject);
    detail.innerHTML = '<a>Details</a>';

    return container;
}

const init = function (this: any) {
    webglNode = document.getElementById('webgl') as HTMLElement;
    aboutNode = document.getElementById('about') as HTMLElement;
    selectedWorkNode = document.getElementById('selectedWorks') as HTMLElement;
    caseStudyNode = document.getElementById('caseStudies') as HTMLElement;
    titleNode = document.getElementById('title') as HTMLElement;

    const selectedWorksContainer = document.getElementById('selectedWorks') as HTMLElement;
    const caseStudiesContainer = document.getElementById('caseStudies') as HTMLElement;

    for (let i = 0; i < contentJSON['projects'].length; i++) {
        const s = contentJSON['projects'][i];
        const scriptNode = buildContentContainerNode(s, i);

        if (s.category.includes("case_study"))
            caseStudiesContainer.append(scriptNode);
        else if (s.category.includes("selected_works"))
            selectedWorksContainer.append(scriptNode);
    }

    // Add all webgl work nodes to focusTargetnodes for raycasting
    fillRaycastingNode(aboutNode);
    fillRaycastingNode(webglNode);

    // TODO: make dynamic
    webglIFrameURLNodes.GLITCH_SKULL.node = document.getElementById('iframe_GLITCH_SKULL') as HTMLElement;
    webglIFrameURLNodes.FUZZY_BLOB.node = document.getElementById('iframe_FUZZY_BLOB') as HTMLElement;
    webglIFrameURLNodes.BAD_SIGNALS.node = document.getElementById('iframe_BAD_SIGNALS') as HTMLElement;
    webglIFrameURLNodes.particleEqualizer.node = document.getElementById('iframe_particleEqualizer') as HTMLElement;

    onScroll();
}
document.addEventListener('DOMContentLoaded', init, false);

let raycastTargetNodes: HTMLElement[] = [];
let aboutNode: HTMLElement;
let webglNode: HTMLElement;
let selectedWorkNode: HTMLElement;
let caseStudyNode: HTMLElement;
let titleNode: HTMLElement;

const webglIFrameURLNodes: any = {
    GLITCH_SKULL: {
        node: null,
        url: "https://avseoul.net/GLITCH_SKULL/"
    },
    FUZZY_BLOB: {
        node: null,
        url: "https://avseoul.net/FUZZY_BLOB/"
    },
    BAD_SIGNALS: {
        node: null,
        url: "https://avseoul.net/BAD_SIGNALS/"
    },
    particleEqualizer: {
        node: null,
        url: "https://avseoul.net/particleEqualizer/"
    },
}

const toggleWebGLDemo = function (name: string, enabled: boolean) {
    if (webglIFrameURLNodes[name].node.src !== webglIFrameURLNodes[name].url && enabled) {
        webglIFrameURLNodes[name].node.src = webglIFrameURLNodes[name].url;
    } else if (webglIFrameURLNodes[name].node.src !== "" && !enabled) {
        webglIFrameURLNodes[name].node.src = "";
    }
}

const fillRaycastingNode = function (sourceNode: HTMLElement) {
    for (let childNode of sourceNode.childNodes) {
        if ((childNode as HTMLElement).className?.includes('listContainer')) {
            raycastTargetNodes.push(childNode as HTMLElement);
        }
    }
}

const handleRaycasting = function (max: number) {
    for (let raycastNode of raycastTargetNodes) {
        const nodeBound = raycastNode.getBoundingClientRect();
        const nodePosition = nodeBound.top + window.pageYOffset;
        const name = raycastNode.getAttribute('name') as string;

        // Is visible in viewport?
        if (nodePosition < max && nodePosition > window.scrollY - nodeBound.height) {
            toggleWebGLDemo(name, true);
        } else {
            toggleWebGLDemo(name, false);
        }
    }
}

const onScroll = function () {
    const raycastPosition = window.scrollY + .5 * window.innerHeight;
    handleRaycasting(raycastPosition);

    if (raycastPosition > webglNode.offsetTop) {
        titleNode.innerHTML = "WebGL Demos";
    } else {
        titleNode.innerHTML = "About";
    }

    if (raycastPosition > selectedWorkNode.offsetTop) {
        titleNode.innerHTML = "Selected Works";
    }

    if (raycastPosition > caseStudyNode.offsetTop) {
        titleNode.innerHTML = "Case Studies";
    }
}
window.addEventListener('scroll', onScroll, true);
