export const CONTENT_TYPE = {
    TEXT: 0, IMAGE: 1, VIDEO: 2, REALTIME: 3
}

export const insertNext = function (targetNode: HTMLElement, referenceNode: HTMLElement) {
    referenceNode?.parentNode?.insertBefore(targetNode, referenceNode.nextSibling);
};

export const getImageAssetURL = function (imageAssets: any[], imageSrc: string) {
	const imageAssetsKey = imageSrc.split(".")[0] as any;
    return Object.entries(imageAssets[imageAssetsKey])[0][1];
}

export const getRandomColorCSS = function() {
	return 'rgb(' + Math.floor(Math.random() * 255) + ',' + Math.floor(Math.random() * 255) + ',' + Math.floor(Math.random() * 255) + ')';
}

export const shuffleRandomScreeningContent = function () {
    const seed = Math.floor(Math.random() * CONTET_JAR.length);
    const m = CONTET_JAR[seed];

    const targetDiv = document.getElementById("random_selected_works") as HTMLElement;

    const w = parseFloat(getComputedStyle(targetDiv.parentNode as HTMLElement, null).getPropertyValue('width'));
    const h = w * .6666;

    targetDiv.style['width'] = w + 'px';
    targetDiv.style['height'] = h + 'px';
    targetDiv.style['background'] = '#000';

    targetDiv.innerHTML =
        '<iframe src=\"https://player.vimeo.com/video/'
        + m.url + '\" width=\"' + w + '\" height=\"' + h
        + '\" frameborder=\"0\" webkitallowfullscreen mozallowfullscreen allowfullscreen></iframe>';
}

export const linksAlive = function () {
    const links = [].slice.call(document.querySelectorAll("a")) as any[];

    for (const link of links) {
        if (link.className != "is_fucked") {
            link.style["color"] = getRandomColorCSS();
            link.style.backgroundColor = getRandomColorCSS();
        }
    }
};

export const renderRainbow = function () {
    const paragraphs = [].slice.call(document.querySelectorAll("p")) as any[];
    const titles = [].slice.call(document.querySelectorAll("av_title")) as any[];
    const links = [].slice.call(document.querySelectorAll("a")) as any[];

    for (const p of paragraphs) {
        p.style.backgroundColor = getRandomColorCSS();

        const words = p.textContent.split(" ");
        p.textContent = '';
        for (const w of words) {
            const tag = document.createElement("rainbow");
            tag.style.color = getRandomColorCSS();

            tag.style.backgroundColor = getRandomColorCSS();

            tag.textContent = w + ' ';
            p.appendChild(tag);
        }
    }

    for (const t of titles) {
        const words = t.textContent.split(" ");
        t.textContent = '';
        for (const w of words) {
            const tag = document.createElement("rainbow");
            tag.style.color = getRandomColorCSS();
            tag.style.backgroundColor = getRandomColorCSS();
            tag.textContent = w + ' ';

            t.appendChild(tag);
        }
    }

    for (const l of links) {
        l.className = "is_fucked";
        l.style.backgroundColor = getRandomColorCSS();
    }
}

export const buildVimeoEmbedCode = function (url: string, showInfo: boolean) {
	const n = showInfo ? 1 : 0;
	return `
		<div style="height: 100%;position:relative;">
			<iframe src="${url}?color=ffffff&title=${n}&byline=${n}&portrait=${n}&controls=${n}" style="position:absolute;top:0;left:0;width:100%;height:100%;" frameborder="0" allow="autoplay" allowfullscreen></iframe>
		</div>
		<script src="https://player.vimeo.com/api/player.js"></script>
	`;
}